import React, { useContext, useEffect, useState } from 'react';
import ReactDOM from 'react-dom';
import { ContextoAlumnos } from '../../../context/ContextoAlumnos';

import ModalAgregarForm from './ModalAgregarForm'
import ModalAgregarBotones from './ModalAgregarBotones'


const ModalAgregar = () => {    
    const [etapa, setEtapa]  = useState('adulto')
    const [diciplina, setDiciplina]  = useState('yoga')  
    const {setModalAgregar, setPeticion} = useContext(ContextoAlumnos)


    function handlerDiciplina (e) {

      switch (e.target.value) {
        case 'yoga':
          setDiciplina('yoga')
        break;

        case 'aikido':
          setDiciplina('aikido')
        break;

        case 'kempo':
          setDiciplina('kempo')
        break;

        case 'karate':
          setDiciplina('karate')
        break;
        
        default:
          setDiciplina('')
        break;
      }

    }

    function handlerKodomo(e) {

      switch (e.target.value) {
        case 'kodomo':
          setEtapa('kodomo')
        break;

        case 'preadolecente':
          setEtapa('preadolecente')
        break;

        case 'adulto':
          setEtapa('adulto')
        break;
        
        default:
          setDiciplina(false)
        break;
      }

    }

    function handlerDisabled(event, id) {

      const input = document.querySelector(`#${id}`)

      if(event.target.checked) {
        input.removeAttribute('disabled')
      } else {
        input.setAttribute('disabled', "")
      }
    }


    function handlerEliminarForm() {
      setPeticion(false)

      setTimeout(() => {
        setModalAgregar(false)        
      }, 1000);

    }

   return ReactDOM.createPortal(
     <div
       className="modal fade "
       id="modal_agregar"
       data-bs-backdrop="static"
       tabIndex="-1"
       aria-labelledby="exampleModalLabel"
       aria-hidden="true"
     >
       <div className="modal-dialog modal-lg">
         <div className="modal-content">
           <div className="modal-header">
             <h5 className="modal-title" id="exampleModalLabel">
               Agregar Alumno
             </h5>
             <button
               type="button"
               className="btn-close"
               data-bs-dismiss="modal"
               aria-label="Close"
               onClick={handlerEliminarForm}
             ></button>
           </div>

           {/* {modalAgregar && (
             <ModalAgregarForm
               handlerDisabled={handlerDisabled}
               handlerKodomo={handlerKodomo}
               handlerDiciplina={handlerDiciplina}
               etapa={etapa}
               diciplina={diciplina}
             />
           )} */}

           <ModalAgregarForm
             handlerDisabled={handlerDisabled}
             handlerKodomo={handlerKodomo}
             handlerDiciplina={handlerDiciplina}
             etapa={etapa}
             diciplina={diciplina}
           />

           <ModalAgregarBotones
             etapa={etapa}
             diciplina={diciplina}
             esAgregando={true}
             textoBoton={'Agregar alumno'}
           />
         </div>
       </div>
     </div>,
     document.getElementById("modal")
   );
}
 
export default ModalAgregar;