import { useState } from "react";
import { Routes, Route, Navigate } from "react-router-dom";
import { getAuth, onAuthStateChanged } from "firebase/auth";
import appFirebase from './credencialesFirebase';

import Home from './components/Home';
import Login from './components/Login';
import ModalAgregar from './components/modal/ModalAgregar/ModalAgregar';
import ModalCuotas from './components/modal/ModalCuotas';
import ModalVerInfo from './components/modal/ModalVerInfo';
import Hero from './components/Hero';
import { ContextoAlumnos } from "./context/ContextoAlumnos";
import { alumnoVacio} from './helpers/ajax';
import { obtenerFirebase } from "./helpers/fireStoreCRUD";
import ModalEstaSeguro from "./components/modal/ModalEstaSeguro";

const auth = getAuth(appFirebase);

const App = () => {

  // const {usuarioGlobal, setUsuarioGlobal} = useContext(ContextoAlumnos)

  // Estados
    // Usuario logueado
  const [usuarioGlobal, setUsuarioGlobal] = useState(null)
  
    // Alumnos
  const [alumnos, setAlumnos] = useState([])
  
   // Alumno Seleccionado
  const [alumnoSeleccionado, setAlumnoSeleccionado] = useState(alumnoVacio)

  // Modal Agregar
  const [modalAgregar, setModalAgregar] = useState(false)

  // Peticiones
  const [peticion, setPeticion] = useState(false)
  const [respuesta, setRespuesta] = useState(false)
  
  

  
  // Verifica Firebase si cambia el estado el usuario
  onAuthStateChanged(auth, (user) => {
    if (user) {
      // si esta iniciado
      setUsuarioGlobal(user) 
    } else {
      // si esta no se inicia sesion
      setUsuarioGlobal(null) 
    }
  });

  // Contexto de alumnos


  return (   
  <ContextoAlumnos.Provider value={{
    alumnos, 
    setAlumnos, 
    alumnoSeleccionado, 
    setAlumnoSeleccionado, 
    setUsuarioGlobal, 
    modalAgregar, 
    setModalAgregar,
    peticion, 
    setPeticion,
    respuesta, 
    setRespuesta
    }}>    
    <Routes>
      <Route path="/" element={<Hero usuarioGlobal={usuarioGlobal}/>} />
      {/* <Route path="/" element={usuarioGlobal ? <Navigate to='/home'/> : <Navigate to='/login'/>} /> */}
      <Route path="home" element={usuarioGlobal ? <Home/> : <Navigate to='/'/>}/>
      <Route path="/login" element={usuarioGlobal ? <Navigate to='/home'/> : <Login/>} />
    </Routes>

    {/* {modalAgregar && <ModalAgregar/>} */}
    <ModalAgregar/>
    <ModalCuotas />
    <ModalVerInfo/>
    <ModalEstaSeguro/>
  </ContextoAlumnos.Provider> 
  );
}
 
export default App;