import React, { useContext } from 'react';
import ReactDOM from 'react-dom';
import { ContextoAlumnos } from '../../context/ContextoAlumnos';
import useFetch from '../../hooks/useFetch';
import {ModalCuotasItemPago} from './ModalItem';




const ModalEstaSeguro = () => {
   const {alumnoSeleccionado, setAlumnoSeleccionado} = useContext(ContextoAlumnos)
   const {eliminarAlumno} = useFetch()

   return ReactDOM.createPortal(
     <div
       className="modal fade "
       id="modal_estaSeguro"
       data-bs-backdrop="static"
       tabIndex="-1"
       aria-labelledby="exampleModalLabel"
       aria-hidden="true"
     >
       <div className="modal-dialog modal-md">
         <div className="modal-content ">
           <div className="modal-header ">
             <h5 className="modal-title" id="exampleModalLabel">
               Importante
             </h5>
             <button
               type="button"
               className="btn-close"
               data-bs-dismiss="modal"
               aria-label="Close"
             ></button>
           </div>
           <div className="modal-body ">   
           <div className="d-flex flex-wrap justify-content-evenly">
            {/* {alumnoSeleccionado.cuotas.map((elem) => {
                return <ModalCuotasItemPago key={elem} cuotas={elem} />;
            })}  */}
           </div>       
             <div>
               <h5 className="mb-4">¬øDesea eliminar a {alumnoSeleccionado.nombre}?</h5>               
             </div>
           </div>
           <div className="modal-footer">
             <button type="button" 
              className="btn btn-damage" 
              data-bs-dismiss="modal" 
              onClick={() => eliminarAlumno(alumnoSeleccionado.id)}>
               Si
             </button>
             <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">
               No
             </button>              
           </div>
         </div>
       </div>
     </div>,
     document.getElementById("modal")
   );
}
 
export default ModalEstaSeguro;