import React, { useContext, useEffect, useLayoutEffect, useState } from 'react';
import ReactDOM from 'react-dom';
import { ContextoAlumnos } from '../../context/ContextoAlumnos';
import { calcularFechaExamen, convertirUnixAFecha } from '../../helpers/helpersTiempoUnix';
import useFetch from '../../hooks/useFetch';
import ModalAgregarBotones from './ModalAgregar/ModalAgregarBotones';
import {ModalVerInfoItem, ModalVerInfoItemGraduacion} from './ModalItem';




const ModalVerInfo = () => {
   const {alumnoSeleccionado} = useContext(ContextoAlumnos);
   const [alumnoSeleccionadoFecha, setAlumnoSeleccionadoFecha] = useState([]);
   const [tiempoTranscurrido, setTiempoTranscurrido] = useState({});
   const { editarAlumno } = useFetch();

  //  console.log(alumnoSeleccionado);

   useEffect(() => {

    if(alumnoSeleccionado.id !== 0) {
      let fechasConvertidas = convertirUnixAFecha(alumnoSeleccionado)
      let tiempoTranscurrido = calcularFechaExamen(alumnoSeleccionado)
  
      setAlumnoSeleccionadoFecha(fechasConvertidas)
      setTiempoTranscurrido(tiempoTranscurrido)
    }

   }, [alumnoSeleccionado]);
   

   return ReactDOM.createPortal(
     <div
       className="modal fade "
       id="modal_verInfo"
       data-bs-backdrop="static"
       tabIndex="-1"
       aria-labelledby="exampleModalLabel"
       aria-hidden="true"
     >
       <div className="modal-dialog modal-lg">
         <div className="modal-content ">
           <div className="modal-header ">
             <h5 className="modal-title" id="exampleModalLabel">
               {alumnoSeleccionado.nombre}
             </h5>
             <button
               type="button"
               className="btn-close"
               data-bs-dismiss="modal"
               aria-label="Close"
             ></button>
           </div>
           <div className="modal-body ">   
           <div className="container">
               <ModalVerInfoItem etiqueta="Nombre" propiedad={alumnoSeleccionado.nombre} identificador="input-nombre-edicion"/>
               <ModalVerInfoItem etiqueta="D.N.I." propiedad={alumnoSeleccionado.dni} identificador="input-dni-edicion"/>
               <ModalVerInfoItem etiqueta="Nacionalidad" propiedad={alumnoSeleccionado.nacionalidad} identificador="input-nacionalidad-edicion"/>
               <ModalVerInfoItem etiqueta="Fecha Nacimiento" propiedad={alumnoSeleccionado.fechaNacimiento} identificador="input-fechaNacimiento-edicion"/>
               <ModalVerInfoItem etiqueta="Fecha de Ingreso" propiedad={alumnoSeleccionado.fechaIngreso} identificador="input-fechaIngreso-edicion"/>
               <ModalVerInfoItem etiqueta="Localidad" propiedad={alumnoSeleccionado.localidad} identificador="input-localidad-edicion"/>
               <ModalVerInfoItem etiqueta="Domicilio" propiedad={alumnoSeleccionado.domicilio} identificador="input-domicilio-edicion"/>
               <ModalVerInfoItem etiqueta="Telefono" propiedad={alumnoSeleccionado.telefono} identificador="input-telefono-edicion"/>
               <ModalVerInfoItem etiqueta="Email" propiedad={alumnoSeleccionado.email} identificador="input-email-edicion"/>
               <ModalVerInfoItem etiqueta="Profesion" propiedad={alumnoSeleccionado.profesion} identificador="input-profesion-edicion"/>
               <ModalVerInfoItem etiqueta="Diciplina" propiedad={alumnoSeleccionado.diciplina} identificador="input-diciplina-edicion"/>
               <ModalVerInfoItem etiqueta="Etapa" propiedad={alumnoSeleccionado.etapa} identificador="input-etapa-edicion"/>
               <ModalVerInfoItem etiqueta="Dojo" propiedad={alumnoSeleccionado.dojo} identificador="input-dojo-edicion"/>
               <ModalVerInfoItem etiqueta="Instructor" propiedad={alumnoSeleccionado.instructor} identificador="input-instructor-edicion"/>
               <ModalVerInfoItem etiqueta="Numero Socio OAA" propiedad={alumnoSeleccionado.numeroSocio} identificador="input-numeroSocio-edicion"/>
               <ModalVerInfoItem etiqueta="Numero AF" propiedad={alumnoSeleccionado.numeroAF} identificador="input-numeroAF-edicion"/>
               <ModalVerInfoItem etiqueta="Aikikai Membership" propiedad={alumnoSeleccionado.numeroMembership} identificador="input-numeroMembership-edicion"/>
               <ModalVerInfoItem etiqueta="Tipo" propiedad={alumnoSeleccionado.tipo} identificador="input-tipo-edicion"/>
               <ModalVerInfoItem etiqueta="Actividad" propiedad={alumnoSeleccionado.actividad} identificador="input-actividad-edicion"/>
               <ModalVerInfoItem etiqueta="Graduacion" propiedad={alumnoSeleccionado.graduacionActual} identificador=""/>
               <ModalVerInfoItem etiqueta="Tiempo Transcurrido" propiedad={`${tiempoTranscurrido.tiempoTranscurrido} a√±os`} identificador=""/>
               {tiempoTranscurrido.estaParaExaminar && <div className='text-center text-success'>Listo para rendir</div>}
           </div>           
             <hr></hr>
             <div>
               <h5 className="mb-4">Fecha de Examenes</h5>
               <div className="container">
                 <div className="row justify-content-center">
                  <ModalVerInfoItemGraduacion etiqueta="9 kyu" propiedad={alumnoSeleccionadoFecha[0]} identificador="input-novenoKyu-edicion"/>                   
                  <ModalVerInfoItemGraduacion etiqueta="8 kyu" propiedad={alumnoSeleccionadoFecha[1]} identificador="input-octavoKyu-edicion"/>
                  <ModalVerInfoItemGraduacion etiqueta="7 kyu" propiedad={alumnoSeleccionadoFecha[2]} identificador="input-septimoKyu-edicion"/>
                  <ModalVerInfoItemGraduacion etiqueta="6 kyu" propiedad={alumnoSeleccionadoFecha[3]} identificador="input-sextoKyu-edicion"/>
                  <ModalVerInfoItemGraduacion etiqueta="5 Kyu" propiedad={alumnoSeleccionadoFecha[4]} identificador="input-quintoKyu-edicion"/>
                  <ModalVerInfoItemGraduacion etiqueta="4 kyu" propiedad={alumnoSeleccionadoFecha[5]} identificador="input-cuartoKyu-edicion"/>
                  <ModalVerInfoItemGraduacion etiqueta="3 kyu" propiedad={alumnoSeleccionadoFecha[6]} identificador="input-tercerKyu-edicion"/>
                  <ModalVerInfoItemGraduacion etiqueta="2 kyu" propiedad={alumnoSeleccionadoFecha[7]} identificador="input-segundoKyu-edicion"/>
                  <ModalVerInfoItemGraduacion etiqueta="1 kyu" propiedad={alumnoSeleccionadoFecha[8]} identificador="input-primerKyu-edicion"/>
                  <ModalVerInfoItemGraduacion etiqueta="1 Dan" propiedad={alumnoSeleccionadoFecha[9]} identificador="input-primerDan-edicion"/>
                  <ModalVerInfoItemGraduacion etiqueta="2 Dan" propiedad={alumnoSeleccionadoFecha[10]} identificador="input-segundoDan-edicion"/>
                  <ModalVerInfoItemGraduacion etiqueta="3 Dan" propiedad={alumnoSeleccionadoFecha[11]} identificador="input-tercerDan-edicion"/>
                  <ModalVerInfoItemGraduacion etiqueta="4 Dan" propiedad={alumnoSeleccionadoFecha[12]} identificador="input-cuartoDan-edicion"/> 
                 </div>
                   
               </div>           
             </div>
           </div>
           <ModalAgregarBotones
             etapa={''}
             diciplina={''}
             esAgregando={false}
             textoBoton={'Actualizar alumno'}
           />
           {/* <div className="modal-footer">
             <button type="button" 
                     className="btn btn-primary" 
                     data-bs-dismiss="modal"
                     onClick={handlerEditarAlumno}
                     >
                       
               Editar alumno
             </button>       
             <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">
               Close
             </button>      
           </div> */}
         </div>
       </div>
     </div>,
     document.getElementById("modal")
   );
}
 
export default ModalVerInfo;