function agregarAlumno(etapas, diciplinas) {

   let graduacionArray = [];

   const diciplina = diciplinas;
   const etapa = etapas;

   let alumno = {
      nombre: document.querySelector('#input-nombre').value,
      documento: document.querySelector('#input-documento').value,
      nacionalidad: document.querySelector('#input-nacionalidad').value,
      fechaNacimiento: document.querySelector('#input-fechaNacimiento').value,
      fechaIngreso: document.querySelector('#input-fechaIngreso').value,
      localidad: document.querySelector('#input-localidad').value,
      domicilio: document.querySelector('#input-domicilio').value,
      telefono: document.querySelector('#input-telefono').value,
      email: document.querySelector('#input-email').value,
      profesion: document.querySelector('#input-profesion').value,
      dojo: document.querySelector('#input-dojo').value,
      instructor: document.querySelector('#input-instructor').value,
      tipo: document.querySelector('#input-tipo').value,
      actividad: document.querySelector('#input-actividad').value,
      numeroSocio: document.querySelector('#input-numeroSocio').value,
      numeroAF: document.querySelector('#input-numeroAF').value,
      numeroMembership: document.querySelector('#input-numeroMembership').value,
      diciplina: diciplina,
      etapa: etapa,
      graduacionActual: '',
      cuota: [],
      novenoKyu:0,
      octavoKyu:0,
      septimoKyu:0,
      sextoKyu:0,
      quintoKyu:0,
      cuartoKyu:0,
      tercerKyu:0,
      segundoKyu:0,
      primerKyu:0,
      primerDan:0,
      segundoDan:0,
      tercerDan:0,
      cuartoDan:0,   
   }
   

   if(diciplina !== 'yoga') {
      
      if(etapa === 'kodomo') {
         const novenoKyuTiempoUnix = Date.parse(document.querySelector('#input-novenoKyu').value) || 0;
         const octavoKyuTiempoUnix = Date.parse(document.querySelector('#input-octavoKyu').value) || 0;
         const septimoKyuTiempoUnix = Date.parse(document.querySelector('#input-septimoKyu').value) || 0;
         const sextoKyuTiempoUnix = Date.parse(document.querySelector('#input-sextoKyu').value) || 0;

         alumno.novenoKyu = novenoKyuTiempoUnix
         alumno.octavoKyu = octavoKyuTiempoUnix
         alumno.septimoKyu = septimoKyuTiempoUnix
         alumno.sextoKyu = sextoKyuTiempoUnix

         graduacionArray.push({nombre: '9 Kyu', fecha: novenoKyuTiempoUnix}, {nombre: '8 Kyu', fecha: octavoKyuTiempoUnix}, {nombre: '7 Kyu', fecha: septimoKyuTiempoUnix}, {nombre: '6 Kyu', fecha: sextoKyuTiempoUnix})

      }   

      const quintoKyuTiempoUnix = Date.parse(document.querySelector('#input-quintoKyu').value) || 0;
      const cuartoKyuTiempoUnix = Date.parse(document.querySelector('#input-cuartoKyu').value) || 0;
      const tercerKyuTiempoUnix = Date.parse(document.querySelector('#input-tercerKyu').value) || 0;
      const segundoKyuTiempoUnix = Date.parse(document.querySelector('#input-segundoKyu').value) || 0;
      const primerKyuTiempoUnix = Date.parse(document.querySelector('#input-primerKyu').value) || 0;
      const primerDanTiempoUnix = Date.parse(document.querySelector('#input-primerDan').value) || 0;
      const segundoDanTiempoUnix = Date.parse(document.querySelector('#input-segundoDan').value) || 0;
      const tercerDanTiempoUnix = Date.parse(document.querySelector('#input-tercerDan').value) || 0;
      const cuartoDanTiempoUnix = Date.parse(document.querySelector('#input-cuartoDan').value) || 0;

      alumno.quintoKyu =  quintoKyuTiempoUnix
      alumno.cuartoKyu =  cuartoKyuTiempoUnix
      alumno.tercerKyu =  tercerKyuTiempoUnix
      alumno.segundoKyu =  segundoKyuTiempoUnix
      alumno.primerKyu =  primerKyuTiempoUnix
      alumno.primerDan =  primerDanTiempoUnix
      alumno.segundoDan =  segundoDanTiempoUnix
      alumno.tercerDan =  tercerDanTiempoUnix
      alumno.cuartoDan =  cuartoDanTiempoUnix

      graduacionArray.push({nombre: '5 Kyu', fecha: quintoKyuTiempoUnix}, {nombre: '4 Kyu', fecha: cuartoKyuTiempoUnix}, {nombre: '3 Kyu', fecha: tercerKyuTiempoUnix}, {nombre: '2 Kyu', fecha: segundoKyuTiempoUnix}, {nombre: '1 Kyu', fecha: primerKyuTiempoUnix}, {nombre: '1 Dan', fecha: primerDanTiempoUnix}, {nombre: '2 Dan', fecha: segundoDanTiempoUnix}, {nombre: '3 Dan', fecha: tercerDanTiempoUnix}, {nombre: '4 Dan', fecha: cuartoDanTiempoUnix})

      let tiempoUnix = 1;
      // let index;
   
      graduacionArray.forEach((elem) => {
   
        if (elem.fecha > tiempoUnix) {
          tiempoUnix = elem.fecha;
         //  index = id;
        } else {
          if (elem.fecha !== 0) {
            return tiempoUnix !== elem.fecha; // corta el bucle
          }
        }
   
      });

      if(
         alumno.novenoKyu === 0 &&
         alumno.octavoKyu === 0 &&
         alumno.septimoKyu === 0 &&
         alumno.sextoKyu === 0 &&
         alumno.quintoKyu === 0 &&
         alumno.cuartoKyu === 0 &&
         alumno.tercerKyu === 0 &&
         alumno.segundoKyu === 0 &&
         alumno.primerKyu === 0 &&
         alumno.primerDan === 0 &&
         alumno.segundoDan === 0 &&
         alumno.tercerDan === 0 &&
         alumno.cuartoDan === 0 ) {
            
            alumno.graduacionActual = '';
            
         } else {
            
            console.log('graduacion actual llena');
            const graduacionAtual = graduacionArray.find((elem) => elem.fecha === tiempoUnix ).nombre
            alumno.graduacionActual = graduacionAtual
         }
   
      // const graduacionAtual = graduacionArray.find((elem) => elem.fecha === tiempoUnix ).nombre
   
      // alumno.graduacionActual = graduacionAtual
   }

   

   return alumno
}

// Limpiar Los campos del Modal Agregar una vez que se hizo la peticion.
function limpiarInput(etapa, diciplina) {
   
   document.querySelector('#input-nombre').value = ''
   document.querySelector('#input-documento').value = ''
   document.querySelector('#input-nacionalidad').value = ''
   document.querySelector('#input-fechaNacimiento').value = ''
   document.querySelector('#input-fechaIngreso').value = ''
   document.querySelector('#input-localidad').value = ''
   document.querySelector('#input-telefono').value = ''
   document.querySelector('#input-email').value = ''
   document.querySelector('#input-profesion').value = ''
   document.querySelector('#input-dojo').value = ''
   document.querySelector('#input-instructor').value = ''
   document.querySelector('#input-tipo').value = ''
   document.querySelector('#input-actividad').value = ''
   document.querySelector('#input-numeroSocio').value = ''
   document.querySelector('#input-numeroAF').value = ''
   document.querySelector('#input-numeroMembership').value = ''


   if(diciplina !== 'yoga') {

      //Input Seleccionar una fecha
      const quinto = document.querySelector('#input-quintoKyu')
      const cuarto = document.querySelector('#input-cuartoKyu')
      const tercero = document.querySelector('#input-tercerKyu')
      const segundo = document.querySelector('#input-segundoKyu')
      const primero = document.querySelector('#input-primerKyu')
      const primerDan = document.querySelector('#input-primerDan')
      const segundoDan = document.querySelector('#input-segundoDan')
      const tercerDan = document.querySelector('#input-tercerDan')
      const cuartoDan = document.querySelector('#input-cuartoDan')

      const inputsGraduaciones = [quinto, cuarto, tercero, segundo, primero, primerDan, segundoDan, tercerDan, cuartoDan]
      
      inputsGraduaciones.forEach(elem => {
         elem.value = ''
         elem.setAttribute('disabled', "")
      })
      
      // Switches
      document.querySelector('#switchQuinto').checked = false
      document.querySelector('#switchCuarto').checked = false
      document.querySelector('#switchTercero').checked = false
      document.querySelector('#switchSegundo').checked = false
      document.querySelector('#switchPrimero').checked = false
      document.querySelector('#switchPrimerDan').checked = false
      document.querySelector('#switchSegundoDan').checked = false
      document.querySelector('#switchTerceroDan').checked = false
      document.querySelector('#switchCuartoDan').checked = false

      if(etapa === 'kodomo') {

         //Input Seleccionar una fecha
         const noveno = document.querySelector('#input-novenoKyu')
         const octavo = document.querySelector('#input-octavoKyu')
         const septimo = document.querySelector('#input-septimoKyu')
         const sexto = document.querySelector('#input-sextoKyu')
         
         const inputsGraduacionesKodomos = [noveno, octavo, septimo, sexto]

         inputsGraduacionesKodomos.forEach((elem) => {
            elem.value = ''
            elem.setAttribute('disabled', "")
         })
         
         
         // Switches
         document.querySelector('#switchNoveno').checked = false
         document.querySelector('#switchOctavo').checked = false
         document.querySelector('#switchSeptimo').checked = false
         document.querySelector('#switchSexto').checked = false
      }
   }

}

