import { useContext } from "react";
import { ContextoAlumnos } from "../context/ContextoAlumnos";
import { agregarAlumno, estructuraAlumnoEdicion } from "../helpers/agregarAlumno";
import { agregarFirebase, borrarFirestore, editarFirestore, obtenerFirebase, obtenerUnAlumnoFirebase } from "../helpers/fireStoreCRUD";

const useFetch = () => {
   const {alumnos, setAlumnos, setPeticion, setRespuesta} = useContext(ContextoAlumnos)

   async function ingresarAlumno(etapa, diciplina) {

      setPeticion(true)

      try {
         const alumno = agregarAlumno(etapa, diciplina)
         await agregarFirebase(alumno)
         setPeticion(false)
         setRespuesta('ok')
      } catch (error) {
         console.log('Error en Agregar Alumno, Mirar abajo el error');
         console.log(error);
         setPeticion(false)
         setRespuesta('error')
      }
      
      return
   }

   async function obtenerAlumnos(filtro = 'nombre', valor = '') {
      console.log('use fetchssss');
      setPeticion(true)
      let alumnosStore = [];
      // let alumnosStore = await obtenerFirebase()

      await fetch(`/app/obtenerAlumnos?filtro=${filtro}&valor=${valor}`)
        .then((response) => response.json())
        .then((data) => {
           console.log(data);
            alumnosStore = [...data]
            setPeticion(false)
        })
        .catch((error) => {
           console.log(error)
           setPeticion(false)
         });


      setAlumnos([...alumnosStore])
      // setPeticion(false)
      // console.log(alumnos);
   }

   async function eliminarAlumno(id) {
      try {
         await borrarFirestore(id)
         const listaAlumnosNueva = alumnos.filter(alumno => alumno.id !== id)
         console.log(listaAlumnosNueva);
         setAlumnos([...listaAlumnosNueva])
         
      } catch (error) {
         console.log('Error al eliminar alumno... ', error)
      }
      
   }

   async function obtenerUnAlumno(nombre) {
      setPeticion(true)

      try {
         let alumnosBuscados = await obtenerUnAlumnoFirebase(nombre);
         setAlumnos([...alumnosBuscados]);
         setPeticion(false)
      } catch (error) {
         console.log('Error al Obtener un alumno');
         setPeticion(false)
      }
   }

   async function editarAlumno(alumnoSeleccionado) {

      setPeticion(true)
      
      try {
         const alumno = estructuraAlumnoEdicion()
         await editarFirestore(alumnoSeleccionado.id, alumno)
         setPeticion(false)
         setRespuesta('ok')
      } catch (error) {
         console.log('Error al actualizar Alumno, Mirar abajo el error');
         console.log(error);
         setPeticion(false)
         setRespuesta('error')
      }
      
      return
   }


   return {obtenerAlumnos,eliminarAlumno, obtenerUnAlumno, ingresarAlumno, editarAlumno}
}
 
export default useFetch;