import IconInfo from './icons/IconInfo';
import IconEliminar from './icons/IconEliminar';
import IconEditar from './icons/IconEditar';
import IconCuotas from './icons/IconCuotas';
import { useContext, useEffect, useLayoutEffect, useState } from 'react';
import { ContextoAlumnos } from '../context/ContextoAlumnos';
import { calcularFechaExamen } from '../helpers/helpersTiempoUnix';
import SpinnerEstaParaExaminar from './icons/IconCheck';
import useFetch from '../hooks/useFetch';

const TablaItem = ({alumno}) => {
   const {alumnoSeleccionado, setAlumnoSeleccionado, setRespuesta} = useContext(ContextoAlumnos)
   const [estaParaExaminar, setEstaParaExaminar] = useState(false);
      
   useEffect(() => {
      setAlumnoSeleccionado({...alumno})
      let {estaParaExaminar} = calcularFechaExamen(alumno)
      setEstaParaExaminar(estaParaExaminar)
   }, []);


   return ( 
      <tr>
         <td>{alumno.nombre} {estaParaExaminar && <SpinnerEstaParaExaminar />}</td>         
         {window.screen.width > 500 && <td>{alumno.telefono}</td>}         
         {window.screen.width > 900 && <td>{alumno.email}</td>}
         {window.screen.width > 400 && <td>{alumno.diciplina}</td>}
         {window.screen.width > 400 && <td>{alumno.graduacionActual}</td>} 
         <td className="d-flex justify-content-evenly">
         <button className="btn btn-outline-success btn-sm d-flex align-items-center" data-bs-toggle="modal" data-bs-target="#modal_cuotas" onClick={() => setAlumnoSeleccionado({...alumno})}>{window.screen.width > 576 ? 'Ver Cuotas' : <IconCuotas/>}</button>
            <button className="btn btn-secondary btn-sm d-flex align-items-center" data-bs-toggle="modal" data-bs-target="#modal_verInfo" onClick={() => {
               setRespuesta('')
               setAlumnoSeleccionado({...alumno})               
               }}>{window.screen.width > 576 ? 'Ver Info' : <IconInfo/>}</button>
            
            <button className="btn btn-danger btn-sm d-flex align-items-center" onClick={() => setAlumnoSeleccionado({...alumno})}
               // id="boton-modalAgregar" 
               // className="btn btn-success align-items-center" 
               data-bs-toggle="modal"
               data-bs-target="#modal_estaSeguro"
            >{window.screen.width > 576 ? 'Eliminar' : <IconEliminar/>}</button>                   
         </td>
         
      </tr> );
}
 
export default TablaItem;