import { useContext } from 'react';
import { ContextoAlumnos } from '../../../context/ContextoAlumnos';
import { limpiarInput } from '../../../helpers/agregarAlumno';
import useFetch from '../../../hooks/useFetch';

const ModalAgregarBotones = ({etapa, diciplina, esAgregando, textoBoton}) => {

  const {peticion, respuesta, alumnoSeleccionado, setPeticion} = useContext(ContextoAlumnos);
  const { ingresarAlumno, editarAlumno } = useFetch();

  async function handlerAgregarOEditar() {
    if(esAgregando) {
      await ingresarAlumno(etapa, diciplina)
      limpiarInput(etapa, diciplina) // Agrega
    } else {
      editarAlumno(alumnoSeleccionado) // Actualiza
    }
  }

   return (
     <div className="modal-footer">
       {/* Spinner */}
       {peticion && <div className="spinner-border me-3" role="status"></div>}

       {respuesta === 'ok' && (
         <div
           className="alert-success fade show text-center btn alerta-correcta"  role="alert">
           <strong>Agregado Correctamente</strong>
         </div>)}
         
         {respuesta === 'error' && ( <div
           className="alert-danger fade show text-center btn alerta-correcta"  role="alert">
           <strong>Error al agregar</strong>
         </div>)}
       

       <button
         type="button"
         className="btn btn-secondary"
         data-bs-dismiss="modal"
         onClick={()=> setPeticion(false)}
       >
         Close
       </button>

       <button
         type="submit"
         className="btn btn-primary"
         id="btn-guardar"
         onClick={handlerAgregarOEditar}
       >
         {" "}
         {textoBoton}{" "}
       </button>
     </div>
   );
}
 
export default ModalAgregarBotones;