import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import appFirebase from '../credencialesFirebase';
import Header from './Header';

const auth = getAuth(appFirebase);

const Login = () => {

   async function submitHandler(event) {
      event.preventDefault();
      const email = document.querySelector('#InputEmail').value
      const password = document.querySelector('#InputPassword').value
      try {
         const user = await signInWithEmailAndPassword(auth, email, password)
      } catch (error) {
         console.log(error.message)
      }
   }

   return ( 
      <>
         <Header/>
         <div className="container d-flex justify-content-center pt-4">         
            <form className="row py-4 shadow rounded formulario-login" onSubmit={submitHandler}>
               <h2 className="mb-3 text-center">Iniciar Sesion</h2>
               <div className="mb-3">
                  <label htmlFor="exampleInputEmail1" className="form-label">Email</label>
                  <input placeholder="Ingrese su email" type="email" className="form-control" id="InputEmail" aria-describedby="emailHelp"></input>               
               </div>
               <div className="mb-4">
                  <label htmlFor="exampleInputPassword1" className="form-label">Password</label>
                  <input placeholder="Ingrese su contrase√±a" type="password" className="form-control" id="InputPassword"></input>
               </div>
               <div className="d-flex justify-content-center px-3">
                  <button type="submit" className="btn btn-primary col-12">Ingresar</button>
               </div>
            </form>
         </div>
      </>
      
    );
}
 
export default Login;