import { useEffect } from "react";
import { Routes, Route, Navigate,useNavigate } from "react-router-dom";

import Header from './Header';
import Home from './Home';
import Login from './Login';

const Hero = ({usuarioGlobal}) => {
   let navigate = useNavigate();
   
   useEffect(() => {
        if(usuarioGlobal){
         navigate('/home')
      } else {
         navigate('/login')              
      }
   }, [usuarioGlobal])

   return ( 
      <>
         <img src="https://aikidoargentina.org/wp-content/uploads/2020/10/logo_160x160-B.png" className="logoHeroOAA"></img>         
      </>
    );
}
 
export default Hero;