// const oneYear = 31556926; // Equivalente a un a√±o
const oneYear = 31536000000; // Equivalente a un a√±o
const oneMonth = 2629744


const graduacionArray = [
   {nombre: '9 Kyu', propiedad: 'novenoKyu', permanenciaNecesaria: oneMonth * 3},
   {nombre: '8 Kyu', propiedad: 'octavoKyu', permanenciaNecesaria: oneMonth * 3},
   {nombre: '7 Kyu', propiedad: 'septimoKyu', permanenciaNecesaria: oneMonth * 3},
   {nombre: '6 Kyu', propiedad: 'sextoKyu', permanenciaNecesaria: oneMonth * 3},
   {nombre: '5 Kyu', propiedad: 'quintoKyu', permanenciaNecesaria: oneMonth * 3},
   {nombre: '4 Kyu', propiedad: 'cuartoKyu', permanenciaNecesaria: oneMonth * 4},
   {nombre: '3 Kyu', propiedad: 'tercerKyu', permanenciaNecesaria: oneMonth * 6},
   {nombre: '2 Kyu', propiedad: 'segundoKyu', permanenciaNecesaria: oneMonth * 8},
   {nombre: '1 Kyu', propiedad: 'primerKyu', permanenciaNecesaria: oneMonth * 10},
   {nombre: '1 Dan', propiedad: 'primerDan', permanenciaNecesaria: oneYear},
   {nombre: '2 Dan', propiedad: 'segundoDan', permanenciaNecesaria: oneYear * 2},
   {nombre: '3 Dan', propiedad: 'tercerDan', permanenciaNecesaria: oneYear * 3},
   {nombre: '4 Dan', propiedad: 'cuartoDan', permanenciaNecesaria: oneYear * 4},
   
]


function calcularFechaExamen(alumnoSeleccionado) {

   if (alumnoSeleccionado.diciplina !== 'yoga') {

      if ( alumnoSeleccionado.graduacionActual === '') {
         return {tiempoTranscurrido: '--- ', estaParaExaminar: false}

      } else {

         const graduacionActual = alumnoSeleccionado.graduacionActual;
         let graduacionActualPropiedad = graduacionArray.find((elem) => elem.nombre === graduacionActual)
      
      
         // Tiempo transcurrido
         const ultimoExamen = alumnoSeleccionado[graduacionActualPropiedad.propiedad]; // tiempo unix
         const fechaActual = Date.now();
      
         const tiempoTranscurridoUnix = fechaActual - ultimoExamen;
         const tiempoTrancurridoEnAno = (tiempoTranscurridoUnix / oneYear);
      
         // Esta para Rendir
         const estaParaExaminar = (fechaActual - alumnoSeleccionado[graduacionActualPropiedad.propiedad]) >= graduacionActualPropiedad.permanenciaNecesaria
      
      
         return  {tiempoTranscurrido: tiempoTrancurridoEnAno.toFixed(2), estaParaExaminar: estaParaExaminar}

      }

      
   } else {

      return {tiempoTranscurrido: '--- ', estaParaExaminar: false}

   }

}



function convertirUnixAFecha(alumnoSeleccionado) {

   const meses = ['01','02','03','04','05','06','07','08','09','10','11','12'];

   const arrayGraduaciones = [
      alumnoSeleccionado.novenoKyu,
      alumnoSeleccionado.octavoKyu,
      alumnoSeleccionado.septimoKyu,
      alumnoSeleccionado.sextoKyu,
      alumnoSeleccionado.quintoKyu,
      alumnoSeleccionado.cuartoKyu,
      alumnoSeleccionado.tercerKyu,
      alumnoSeleccionado.segundoKyu,
      alumnoSeleccionado.primerKyu,
      alumnoSeleccionado.primerDan,
      alumnoSeleccionado.segundoDan,
      alumnoSeleccionado.tercerDan,
      alumnoSeleccionado.cuartoDan
   ]

   let graduacionesFecha = [];

   arrayGraduaciones.forEach(elem => {

      // Si la fecha no es 0, hacer la conversion
      if (elem !== 0) {
         const unixAFecha = new Date(elem);

         const ano = unixAFecha.getFullYear();
         const mes = meses[unixAFecha.getMonth()];
         const dia = unixAFecha.getDate();

         const fecha = `${dia}/${mes}/${ano}`

         graduacionesFecha.push(fecha);
      } else {
         graduacionesFecha.push('Sin Registro');
      }
      

   })

   return graduacionesFecha;

}

export {convertirUnixAFecha, calcularFechaExamen}