import React, { useContext } from 'react';
import ReactDOM from 'react-dom';
import { ContextoAlumnos } from '../../context/ContextoAlumnos';
import {ModalCuotasItemPago} from './ModalItem';




const ModalAgregar = () => {
   const {alumnoSeleccionado, setAlumnoSeleccionado} = useContext(ContextoAlumnos)

   function agregarCuota(event) {
      event.preventDefault()
      const inputMonto = document.querySelector('#input-monto').value
      const inputFecha = document.querySelector('#input-fecha').value

      let copiaAlumno = alumnoSeleccionado
      copiaAlumno.cuotas.push({monto: inputMonto, fecha: inputFecha})
      setAlumnoSeleccionado({...copiaAlumno})   
      
   }
   

   return ReactDOM.createPortal(
     <div
       className="modal fade "
       id="modal_cuotas"
       data-bs-backdrop="static"
       tabIndex="-1"
       aria-labelledby="exampleModalLabel"
       aria-hidden="true"
     >
       <div className="modal-dialog modal-md">
         <div className="modal-content ">
           <div className="modal-header ">
             <h5 className="modal-title" id="exampleModalLabel">
               {alumnoSeleccionado.nombre}
             </h5>
             <button
               type="button"
               className="btn-close"
               data-bs-dismiss="modal"
               aria-label="Close"
             ></button>
           </div>
           <div className="modal-body ">   
           <div className="d-flex flex-wrap justify-content-evenly">
            {/* {alumnoSeleccionado.cuotas.map((elem) => {
                return <ModalCuotasItemPago key={elem} cuotas={elem} />;
            })}  */}
           </div>           
             <hr></hr>
             <div>
               <h5 className="mb-4">Agregar pago</h5>
               <form className="row justify-content-center">
                 <div className="col-12">
                  <div className="input-group mb-2">
                     <span className="input-group-text">$</span>
                     <input type="text" aria-label="First name" className="form-control" id="input-monto"></input>
                     <input type="date" aria-label="Last name" className="form-control" id="input-fecha"></input>
                     <button className="btn ms-4 rounded btn-success text-white" onClick={agregarCuota} id="botonAgregarCuota">+</button>
                  </div>
                  </div>                   
               </form>
             </div>
           </div>
           <div className="modal-footer">
             <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">
               Close
             </button>             
           </div>
         </div>
       </div>
     </div>,
     document.getElementById("modal")
   );
}
 
export default ModalAgregar;