import { getAuth, signOut } from "firebase/auth";
import appFirebase from '../credencialesFirebase';

import BarraSuperiorTitulo from './BarraSuperiorTitulo';

const auth = getAuth(appFirebase);

const BarraSuperior = ({secciones, setSecciones}) => {
   
   return ( 
      <nav className="container-fluid d-flex justify-content-between p-3 bg-dark bg-opacity-75">
         {secciones !== '' && <button type="button" className="btn btn-outline-light position-relative shadow d-flex align-items-center" onClick={() => setSecciones('')}>
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-chevron-compact-left" viewBox="0 0 16 16">
               <path fillRule="evenodd" d="M9.224 1.553a.5.5 0 0 1 .223.67L6.56 8l2.888 5.776a.5.5 0 1 1-.894.448l-3-6a.5.5 0 0 1 0-.448l3-6a.5.5 0 0 1 .67-.223z"/>
            </svg>
         </button>}          
         <BarraSuperiorTitulo secciones={secciones}/>         
         <button className="btn btn-danger shadow-sm" onClick={() => signOut(auth)}>Cerrar Sesion</button>
       </nav>
    );
}
 
export default BarraSuperior;